article {
  position: relative;
  padding: 2rem 1.5rem 1.5rem;
  border-radius: 0.5rem;
  background-color: #fff;
  box-shadow: 0 0.5rem 1rem hsl(180, 8%, 52%, 0.3);
}

article.featuredCard {
  position: relative;
}

article.featuredCard::before {
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  width: 4px;
  height: 100%;
  border-radius: 0.5rem 0 0 0.5rem;
  background-color: var(--desaturated-dark-cyan);
}

article + article {
  margin-top: 3rem;
}

.logo {
  position: absolute;
  top: -10%;
  width: 44px;
}

.cardHeader {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.company {
  font-size: 1rem;
  color: var(--desaturated-dark-cyan);
}

.badge {
  padding: 0.7em 0.7em 0.3em 0.7em;
  border-radius: 1em;
  text-transform: uppercase;
  font-size: 0.7rem;
  font-weight: 700;
  color: #fff;
}

.newBadge {
  background-color: var(--desaturated-dark-cyan);
}

.featuredBadge {
  background-color: var(--very-dark-grayish-cyan);
}

.position {
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 700;
  color: var(--very-dark-grayish-cyan);
}

.position:hover {
  color: var(--desaturated-dark-cyan);
}

.detail {
  display: flex;
  gap: 1.8rem;
  padding: 0;
}

.detail li {
  font-size: 0.9rem;
  color: var(--dark-grayish-cyan);
}

.detail li:first-child {
  list-style: none;
}

hr {
  height: 1px;
  background-color: hsl(180, 8%, 52%, 0.5);
  border: none;
}

.categoryList {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  padding: 0;
  margin-bottom: 0;
  list-style: none;
}

.categoryList button:hover {
  background-color: var(--desaturated-dark-cyan);
  color: #fff;
}

@media screen and (min-width: 700px) {
  article {
    display: flex;
    gap: 1.5rem;
    align-items: center;
    padding: 1.5rem 2rem;
  }

  .logo {
    position: static;
    width: 88px;
  }

  hr {
    display: none;
  }

  .categoryList {
    margin-left: auto;
  }
}
