.filter {
  display: flex;
  padding: 0.5rem 1.5rem;
  margin-top: -4rem;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 4rem;
  background-color: #fff;
  border-radius: 0.5rem;
  box-shadow: 0 0.5rem 1rem hsl(180, 8%, 52%, 0.3);
}

.filterList {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  padding: 0;
  list-style: none;
}

.filterList li {
  display: flex;
  padding: 0;
  align-items: center;
  border-radius: 0.2rem;
  background-color: var(--filter);
  font-size: 0.9rem;
  font-family: 'Spartan', sans-serif;
  font-weight: 700;
  color: var(--desaturated-dark-cyan);
  overflow: hidden;
}

.filterList li span {
  padding: 0.6em 0.6em 0.5em 0.6em;
}

.btnFilter {
  padding: 0;
  display: flex;
  cursor: pointer;
}

.btnFilter img {
  padding: 0.8em;
  background-color: var(--desaturated-dark-cyan);
}

.btnFilter:hover img {
  padding: 0.8em;
  background-color: var(--very-dark-grayish-cyan);
}

.btnClear {
  margin-left: auto;
  background: none;
  color: var(--dark-grayish-cyan);
}

.btnClear:hover {
  text-decoration: underline;
  color: var(--desaturated-dark-cyan);
}

@media screen and (min-width: 700px) {
  .filter {
    padding: 0 2rem;
  }
}
